<template>
    <div class="container">
        <div  v-for="mass in massage">
            <h1>{{mass.title}}</h1>
            <p>{{mass.text}}</p>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                massage: [
                    {title: 'KAKO DELUJE MASAA ?', text: 'Ukoliko se redovno primenjuje, masaza  deluje blagotvorno na ' +
                    ' kompletan organizam i pritom podstie dobro '+
                        'raspoloenje. Najvei broj bolesti uzrokovan je stresom, a masaa predstavlja jedno od najboljih sredstava '+
                   ' u borbi protiv napetosti i nervoze iji je est uzronik hronini umor. Pomae i u sluaju nesanice, naroito '+
                   ' ako obuhvata celo telo,mada se dobar efekat postie i ukoliko se proimeni samo masaza ledja,masaa stopala ili '+
                   ' masaa vrata. Kod sportista ublaava greve i upalu miia, poveava izdrljivost i pokretljivost, s tim to se '+
                   ' preporuuje pre vebanja,kada treba izmasirati miie koji e biti najaktivniji, ali i nakon treninga kako bi '+
                   ' se ublaili umor i iscrpljenost.' },
                    {title: 'Relaks masaa', text: 'Relax masaa je prijatna masaa koja oputa organizam i sjedinjava protok energije. ' +
                    'Cilj joj je negovanje '+
                    'tela i podizanje opteg zdravstvenog stanja. Za cilj ima takoe prevencija stresa i relaksacija miia. '+
                    'Obuhvata celo telo ili pojedine delove kao to su noge, stopala, lea, grudni ko, vrat ili lice.' },
                    {title: 'Klasicna masaa', text: 'Klasina masaa dovodi do oseaja oputenosti  i poseduje opte smirujue delovanje.'+
                    'esto se koristi i '+
                    'kod sportista, kao zagrevanje i pripremu osobe za jae tehnike, odnosno za sportsku masau. Odlina je '+
                    'za oputanje napetosti u miiima i vraanje energije nakon povrede. '+
                    'Ovom tehnikom temperatura tijela se povisuje za 2-3 stepena, dolazi do poboljanja cirkulacije, ne samo '+
                    'u povrinskim delovima, ve i u potkonom tkivu i miieima, te dolazi do hiperemije tih delova. Poboljava '+
                    'se dotok kiseonika i hranjivih materija, te se pojaava izluivanje vode iz tela i stimulise imunitet.'},
                    {title: 'Sportska masaa', text: 'Ova masaa ubrzava procese eliminacije mlene kiseline nastale prilikom miinog rada te time blagotvorno '+
                    'deluje na oporavak miia.. Sportskom masaom ubrzava se oporavak organizma nakon treninga, '+
                    'a takoe se  miii ovim putem mogu pripremiti za trening. Ova masaza se preporuuje sportistima i rekreativcima.' },
                    {title: 'Maderoterapija', text: 'Maderoterapija ili terapija drvetom je sve popularnija kod nas, a i svetu. Iako se uglavnom ene odlue za '+
                        'ovu masau da bi se reile celulita, ona je korisna i kod bolova u leima i generalno za detoks organizma. '+
                        'Viak masti, viak eera i viak aditiva iz hrane takoe ostaju zarobljeni u naem telu i zajedno stvaraju '+
                    'masne naslage koje lie na pomorandinu koru  celulit. Ono to masaa okalgijama zapravo radi jeste '+
                    'stimulacija limfnog sistema, koji je zaduen za izbacivanje toksina iz naeg tela.' },

                ]
            }
        }
    }
</script>

